ntabilidad sino que incide en una generación de mayores y mejores resultados financieros al implementar esta estrategia, que aparentemente atañe al área de producción, pero que implementado en su operación regular genera cambios aún en el pensamiento y comportamiento del ser humano. La siguiente gráfica expone un flujo de logística directa e inversa para comprender visualmente esta área organizacional.")]),_vm._m(2),_vm._m(3),_c('p',{staticClass:"mt-5",attrs:{"data-aos":"fade-right"}},[_vm._v("En la actualidad las organizaciones se encuentran en constante búsqueda de alternativas que le permitan consolidarse como entidades competitivas, consiguiendo soluciones que les conllevan mayores retos, gracias al entorno que enfrentan como lo son la globalización y los avances tecnológicos. En este sentido, cada organización requiere diseñar una estructura y procesos conforme a las exigencias de los mercados, correspondiendo al postulado de que los bienes o servicios deben ser el resultado de altos estándares de calidad para lograr una mayor competitividad y participación; y esto es posible, cuando se saca el mayor beneficio de los factores internos y externos, que engranados favorecen las relaciones de intercambio en cada uno de los eslabones involucrados con la producción y comercialización de productos.")]),_c('Separador'),_vm._m(4),_c('p',{attrs:{"data-aos":"fade-right"}},[_vm._v("Para tratar el tema del aprovisionamiento necesariamente se debe considerar la cadena de suministros, gracias a su relevancia, la cual radica en el vínculo que existe entre sus elementos, desde el punto de origen del producto o servicio hasta el punto de consumo, lo cual implica el estudio de un proceso a nivel gerencial, que facilita a las organizaciones la promoción a nuevos niveles de competitividad y de rentabilidad.")]),_c('p',{staticClass:"mt-4",attrs:{"data-aos":"fade-left"}},[_vm._v("En este nivel de importancia la gestión de la cadena de suministro debe ser establecida más allá de los l