ucto de valor para el cliente (Calderio, 2018).")]),_c('p',{staticClass:"mt-4",attrs:{"data-aos":"fade-left"}},[_vm._v("En esta fase es donde participan los actores que suministran diferentes insumos para la ejecución de la actividad productiva. Las materias primas serán variadas de acuerdo con el sector al que pertenezca la cadena de suministro, teniendo especial cuidado en cuanto al cumplimiento de tiempos y niveles de inventario para satisfacer las necesidades de producción. Es conocida también con el término adquisición, al llevarse a cabo todas las actividades relacionadas con la compra de productos y materiales de proveedores externos. No obstante, por ser la fase inicial requiere de la planeación de recursos y decisiones contractuales para el suministro a través de técnicas de negociación, recepción y despacho de pedidos, transporte interno, inspección, almacenamiento y aseguramiento de la calidad; sumado a la necesidad de coordinar con proveedores la programación, continuidad de la provisión, protección y garantías contra pérdidas y especulación. También involucra, además, la investigación de nuevas fuentes, con el fin de aportar a la organización la fabricación o reventa a bajos costos, lo cual implica todo un conjunto de procesos organizacionales gestionados eficientemente.")]),_c('div',{staticClass:"col-xl-10 m-auto mt-5"},[_c('div',{staticClass:"bloque-texto-g bloque-texto-g--inverso color-acento-contenido p-3 p-sm-4 p-md-5",attrs:{"data-aos":"flip-up"}},[_c('div',{staticClass:"bloque-texto-g__img",style:({'background-image': ("url(" + (require('@/assets/curso/tema2/8.png')) + ")")})}),_vm._m(7)])]),_c('p',{staticClass:"mt-5",attrs:{"data-aos":"fade-right"}},[_vm._v("En esta fase las decisiones suelen ser cruciales debido a su nivel de influencia en el nivel de eficiencia y capacidad de respuesta, lo que implica conocer la facultad con que cuenta la organización para realizar el proceso de abastecimiento, el costo a invertir al apropiarse e implementarlo